var PostData=function PostData(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_0_react___default.a.Fragment,null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"Hi!  Today is Saturday, February 29 - Leap Year, 2020!  I woke up this morning with a really good idea for a blog post and set out on a mission to write it, so here goes nothing."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"One thing I did not know before beginning my software development career a year ago was the difference between a software consulting company and a regular software company.  Volano was software consulting and so is my current job at Fixation, so now after a year I have a pretty good idea!"),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"The main difference is who owns the product you are working on.  At your typical software company, the company you are working for owns the software, and they make money off of selling it to other people.  For example, I worked at D3 Banking (now owned by NCR) for a year, which was a traditional software company.  They made software which they could customize and sell to banks."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"At a software consulting company, someone else outside the company owns the software.  You are building it for them, but your company does not own it.  This can take a lot of forms.  Sometimes the project is huge - basically you are the tech support team for a company that doesn't have a technical team.  Sometimes the projects are medium-sized or small - build a website that one person has dreamed up.  And sometimes the projects have already been built by another team and you are updating them."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"It is almost like freelancing, except that you typically don't have to find the projects yourself.  The company does the work of finding those projects.  They also have support in place for project management, and other things you'd get at a traditional company (health benefits etc) that you wouldn't get while freelancing.  You can just concentrate on the coding and not have to worry about the overhead stuff.  I kind of think of it as \"a band of developers, coming together for the common good\" :)"),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"So that's the main difference, but how does that translate into the day-to-day operational differences between these companies?  Here are some of the things I've noticed."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("h3",null,"Bill by the hour"),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"The biggest difference is that consulting places bill by the hour.  At the beginning of a project, they will talk with the client and come up with an estimate of how long it will take to complete the project.  As you are going along in the project lifecycle, you are constantly checking to make sure that you're not going over hours."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"This is way different from a normal software company.  At a typical software company, they don't really care that much about how much time a feature takes you - they just say \"get this thing done by X date.\"  But in consulting there are discussions around, how much time do you think it will take to complete this feature?"),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"I have to say - I am not a huge fan of this system.  Sometimes things that seem simple (ahem CSS ahem) can take an enormous amount of time to figure out.  And sometimes things that seem big and scary and complex take less time.  Maybe over time I will get better at estimating - and I think I have already - but there are always going to be surprises.  But, what am I going to do, it is what it is and I'm learning to work with it."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("h3",null,"More client facetime"),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"Another big difference between consulting and typical software development is the amount of facetime you get with the clients.  In all my other jobs, they would typically keep us technical folk locked away in a back room, as far away from the people buying the software as they could get us!  Not really but they did have seasoned pros talking with the clients 99% of the time."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"In consulting you can be working on smaller projects, so there is often no one between the client and you.  I've worked on some sites where the client was calling me up directly and we'd chat about a feature they wanted and the feasibility of it."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"It can be difficult.  I think you need to be pretty emotionally intelligent to handle this part of the job.  You need to be able to read the client and figure out how things are REALLY going with them during meetings, to be able to prevent a potential blowup down the line.  And you need to know yourself and conduct yourself professionally during meetings, even if you are feeling frustrated or some other negative emotion."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("h3",null,"Context switching"),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"Another thing that is different from a typical software development gig is the amount of context switching you'll have to do in consulting.  In consulting, you could be working on a new project, while playing a support role on one of the older projects.  If the client calls you in the middle of work on new development, well, sometimes you have to drop what you're doing and go work on that.  It's the nature of the beast.  In a traditional software company you wouldn't have that as much.  You for sure don't have clients calling you up at all hours asking you to complete tasks for them.  And they typically seem to assign people to one or two projects at a time, and that's it."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"I'd say that time management skills are important in a consulting role.  To me this means, being a professional and when something comes up and you can't make a deadline, being honest with your project manager or client about it and working out a solution instead of hiding it and powering through.  If you're working at a good place they'll understand."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("h3",null,"Exposure to the full stack"),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"The last thing that I've seen that is different from a typical software company is that you are typically a \"jack of all trades\" in consulting.  At my current job, I am a full stack developer.  I'm working on new development that we are building with Angular for part of the front end, ASP.NET Framework MVC for server side, SQL Server Management Studio for the database, and hosting on Azure.  And XUnit for unit testing (the most important part if you ask me ;) )"),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"At a typical software company, they have people split up into roles.  They'll have a front end developer, a server side developer, quality assurance engineers, devops.  For each of those roles you can get really in depth with it.  Front end development has become it's own monster in recent years, with TONS of different frameworks to choose from, and each one has its own suite of tools.  Server-side has its own toolkit, so does devops and QA... etc etc.  You can get really good at any one of these specialties if you are focusing on just that."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"I have only worked at really small consulting firms (19 and 5 employees).  I do think it's possible to have a consulting company that has people who specialize, maybe at a bigger consulting company, but I haven't seen it yet."),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"Well that's what I have for today.  I have another idea for a blog post on consulting companies, but that will have to be a Part 2 on this if it happens."));};/* harmony default export */ __webpack_exports__["default"] = (PostData);

